import {
  LinearProgress,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  TextField,
  Typography,
  Pagination,
  Box,
} from "@mui/material";
import { makeStyles } from "@mui/styles";
import axios from "axios";
import React, { useState } from "react";
import { useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { CoinList } from "../config/api";
import { useSelector } from "react-redux";

export function numberWithCommas(x) {
  return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}
const useStyles = makeStyles({
  row: {
    backgroundColor: "transperant",
    cursor: "pointer",
    "&:hover": {
      backgroundColor: "black",
    },
    fontFamily: "Montserrat",
  },
  pagination: {
    "& .MuiPaginationItem-root": {
      color: "#00ffa1",
    },
  },
});

const CoinsTable = () => {
  const [coins, setCoins] = useState([]);
  const [loading, setLoading] = useState(false);
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);

  const currency = useSelector((state) => state.crypto.value);
  const symbol = useSelector((state) => state.crypto.symbol);

  const navigate = useNavigate();

  const classes = useStyles();

  const fetchCoins = async () => {
    setLoading(true);
    const { data } = await axios.get(CoinList(currency));
    setCoins(data);
    setLoading(false);
  };

  useEffect(() => {
    fetchCoins();
  }, [currency]);

  const handleSearch = () => {
    return coins.filter(
      (coin) =>
        coin.name.toLowerCase().includes(search.toLowerCase()) ||
        coin.symbol.toLowerCase().includes(search.toLowerCase())
    );
  };

  return (
    <Box
      width={{ xs: "98%", sm: "95%", md: "80%" }}
      mx={"auto"}
      sx={{ textAlign: "center" }}
    >
      <Typography
        variant={"h4"}
        pt={4}
        sx={{
          fontFamily: "Montserrat",
          fontSize: { xs: "25px", sm: "42px" },
        }}
      >
        Cryptocurrency Pricess by Market Cap
      </Typography>
      <TextField
        id="outlined-basic"
        label="Search For a Crypto Currency.."
        variant="outlined"
        sx={{
          margin: "20px 0",
          width: "100%",
          borderBottom: "1px solid white",
          "& .MuiOutlinedInput-root": {
            color: "white",
          },
        }}
        focused
        onChange={(e) => setSearch(e.target.value)}
      />

      <TableContainer>
        {loading ? (
          <LinearProgress style={{ backgroundColor: "#00ffa1" }} />
        ) : (
          <Table>
            <TableHead style={{ backgroundColor: "#00ffa1" }}>
              <TableRow>
                {["Coin", "Price", "24h Change"].map((head) => (
                  <TableCell
                    sx={{
                      color: "black",
                      fontWeight: "700",
                      fontFamily: "Montserrat",
                    }}
                    key={head}
                    align={head === "Coin" ? "left" : "right"}
                  >
                    {head}
                  </TableCell>
                ))}
              </TableRow>
            </TableHead>
            <TableBody>
              {handleSearch()
                .slice((page - 1) * 10, (page - 1) * 10 + 10)
                .map((row) => {
                  const profit = row.price_change_percentage_24h > 0;
                  return (
                    <TableRow
                      onClick={() => navigate(`/coins/${row.id}`)}
                      className={classes.row}
                      key={row.name}
                      sx={{
                        "&:last-child td, &:last-child th": { border: 0 },
                      }}
                    >
                      <TableCell
                        component="th"
                        scope="row"
                        style={{
                          display: "flex",
                          gap: 15,
                        }}
                      >
                        <Box sx={{ display: { xs: "none", sm: "block" } }}>
                          <img
                            src={row?.image}
                            alt={row.name}
                            height="50"
                            style={{ marginBottom: 10 }}
                          />
                        </Box>
                        <Box sx={{ display: "flex", flexDirection: "column" }}>
                          <span
                            style={{
                              textTransform: "uppercase",
                              fontSize: 22,
                              color: "white",
                            }}
                          >
                            {row.symbol}
                          </span>
                          <span style={{ color: "darkgrey" }}>{row.name}</span>
                        </Box>
                      </TableCell>
                      <TableCell
                        sx={{
                          color: "white",
                          textAlign: { xs: "center", sm: "right" },
                        }}
                      >
                        {symbol}{" "}
                        {numberWithCommas(row.current_price.toFixed(2))}
                      </TableCell>
                      <TableCell
                        sx={{
                          color: profit > 0 ? "rgb(14, 203, 129)" : "red",
                          fontWeight: 500,
                          textAlign: { xs: "center", sm: "right" },
                        }}
                      >
                        {profit && "+"}
                        {row.price_change_percentage_24h.toFixed(2)}%
                      </TableCell>
                    </TableRow>
                  );
                })}
            </TableBody>
          </Table>
        )}
      </TableContainer>
      <Pagination
        style={{
          padding: 20,
          width: "100%",
          display: "flex",
          justifyContent: "center",
        }}
        className={classes.pagination}
        count={handleSearch()?.length / 10}
        onChange={(_, value) => {
          setPage(value);
          window.scroll(0, 450);
        }}
      />
    </Box>
  );
};

export default CoinsTable;
