import { Stack, Box, Typography, LinearProgress } from "@mui/material";
import { styled } from "@mui/styles";
import axios from "axios";
import { useEffect } from "react";
import { useState } from "react";
import { useParams } from "react-router-dom";
import { numberWithCommas } from "../components/CoinsTable";
import { SingleCoin } from "../config/api";
import { useSelector } from "react-redux";
import { lazy, Suspense } from "react";

const CoinInfo = lazy(() => import("../components/CoinInfo"));

const HeadingStyled = styled(Typography)({
  marginBottom: 20,
  fontFamily: "Montserrat",
});

const CoinPage = () => {
  const { id } = useParams();
  const [coin, setCoin] = useState();

  if (coin?.name) {
    document.title = `Crypto Monster -${coin.name}`;
  }

  const currency = useSelector((state) => state.crypto.value);
  const symbol = useSelector((state) => state.crypto.symbol);

  const fetchCoin = async () => {
    const { data } = await axios.get(SingleCoin(id));
    setCoin(data);
  };

  useEffect(() => {
    fetchCoin();
  }, [currency]);

  if (!coin) return <LinearProgress style={{ backgroundColor: "#00ffa1" }} />;

  return (
    <Stack direction={{ xs: "column", md: "row" }}>
      <Box
        sx={{
          width: { xs: "100%", md: "40%" },
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          marginTop: "25px",
          borderRight: "2px solid grey",
          padding: "0 0 0 10px",
        }}
      >
        <img
          src={coin?.image.large}
          alt={coin?.name}
          height="200"
          style={{ marginBottom: 20 }}
        />
        <Box width={{ xs: "80%", sm: "60%", md: "90%" }}>
          <HeadingStyled align="center" variant="h3" fontWeight={"bold"}>
            {" "}
            {coin?.name}{" "}
          </HeadingStyled>

          <Typography
            my={3}
            variant="subtitle1"
            sx={{ overflowWrap: "anywhere" }}
          >
            {coin?.description.en.split(". ")[0]}.
          </Typography>

          <Stack
            direction={"row"}
            justifyContent={{ xs: "space-between", sm: "normal" }}
          >
            <HeadingStyled variant="h6" fontWeight={"bold"}>
              Rank:&nbsp; &nbsp;
            </HeadingStyled>
            <Typography variant="h6">{coin?.market_cap_rank}</Typography>
          </Stack>
          <Stack
            direction={"row"}
            justifyContent={{ xs: "space-between", sm: "normal" }}
          >
            <HeadingStyled variant="h6" fontWeight={"bold"}>
              Current Price:&nbsp; &nbsp;
            </HeadingStyled>
            <Typography variant="h6">
              {symbol}
              {numberWithCommas(
                coin?.market_data.current_price[currency.toLowerCase()]
              )}
            </Typography>
          </Stack>
          <Stack
            direction={"row"}
            justifyContent={{ xs: "space-between", sm: "normal" }}
          >
            <HeadingStyled variant="h6" fontWeight={"bold"}>
              Market Cap:&nbsp; &nbsp;
            </HeadingStyled>
            <Typography variant="h6">
              {symbol}
              {numberWithCommas(
                coin?.market_data.market_cap[currency.toLowerCase()]
                  .toString()
                  .slice(0, -6)
              )}
              M
            </Typography>
          </Stack>
        </Box>
      </Box>
      <Suspense fallback={""}>
        <CoinInfo coin={coin} />
      </Suspense>
    </Stack>
  );
};

export default CoinPage;
